import React from 'react';
import compose from 'recompose/compose';
import { withApollo, initOnContext } from '../../shared/lib/withApollo';
import HeadTitle from '../../components/HeadTitle';
import Header from '../../components/Header';
import Menu from '../../components/Menu';
import Footer from '../../components/Footer';
import NewsTeaser from '../../components/NewsTeaser';
import withNews, { NEWS_QUERY, newsListMapper } from '../../shared/query/withNews';
import styles from './styles.css';

const NewsOverview = ({
  loading,
  newsList,
}) =>
  (!loading &&
    <div>
      <Menu />
      <HeadTitle title="News" />
      <div className={styles.contentWrapper}>
        <Header />
        <div className={styles.content}>
          <h1 className={styles.title}>News</h1>
          <div className={styles.detail}>
            <ul>
              {newsList
                .sort(
                  (a, b) => {
                    a = moment(a);
                    b = moment(b);
                    return b.publishedDate.format('X') - a.publishedDate.format('X');
                  }
                )
                .map(newsTeaser =>
                  <NewsTeaser key={newsTeaser.id} newsTeaser={newsTeaser} />,
                )}
            </ul>
          </div>
          <Footer />
        </div>
      </div>
    </div>) ||
  <div>loading</div>;

export const getStaticProps = async (ctx) => {
  initOnContext(ctx)

  const result = await ctx.apolloClient.query({
    query: NEWS_QUERY
  });

  return {
    props: {
      loading: false,
      newsList: newsListMapper(result.data.nodeQuery.entities).map(news => ({
        ...news,
        publishedDate: news.publishedDate && publishedDate.valueOf()
      })),
    }
  }
}

export default NewsOverview;
